import React from 'react';


const Alex = {
    position: ["Front-end developer", "Friend", "Student", "Creator"],
    info: {
        age: 21,
        address: "203 Humber College blvd, Toronto",
        email: "alexperesta@gmail.com",
        phone: "647-259-6657",
        availability: 'Free'
    }
};
const listParam = Object.keys(Alex.info).map((number) =>
    <p key={number.toString()}>{number}</p>
);
const listValues = Object.values(Alex.info).map((number) =>
    <p key={number.toString()}>{number}</p>
);


class Card extends React.Component {
    constructor(props) {
        super(props);
        this.state = {position: Alex.position[0],
        didMount: true
        };
    }
    componentDidMount() {
        let i = 0;
        this.timerID = setInterval(
            () => {
                this.setState({
                    position: Alex.position[i],
                    didMount: true
                });
                i++;
                if(i === Alex.position.length-1){
                    i = 0;
                }
                setTimeout(() =>{
                    this.setState({
                        didMount: false
                    });
                }, 3500);

            },
            4000
        );
    }
    componentWillUnmount() {
        clearInterval(this.timerID);
    }
    render() {
        return (
            <div className="mainCard">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-10 cardLeaf">
                            <div className="row mainInfo">
                                <div className="col-md-5 col-12">
                                    <div className="avatar">
                                        <div className="pic"></div>
                                    </div>
                                </div>
                                <div className="col-md-7 col-12">
                                    <div className="chipHello">
                                        <h5>Hello</h5>
                                    </div>
                                    <div className="greeting">
                                        <h2>I'm <span>Oleksandr Peresta</span></h2>
                                        <h5 className={this.state.didMount ? "visible" : "fade-out"}>{this.state.position}</h5>
                                    </div>
                                    <div className="line"></div>
                                    <div className="lineMain"></div>
                                    <div className="moreInfo">
                                        <div className="parameter">
                                            {listParam}
                                        </div>
                                        <div className="value">
                                            {listValues}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 contacts">
                                    <div className="icon">
                                        <a href="https://www.facebook.com/vardjaklap"><i className="fab fa-facebook-f"></i></a>
                                    </div>
                                    <div className="icon">
                                        <a href="https://www.linkedin.com/in/oleksandr-peresta/"><i className="fab fa-linkedin-in"></i></a>
                                    </div>
                                    <div className="icon">
                                        <a href="https://www.instagram.com/alexperesta/"><i className="fab fa-instagram"></i></a>
                                    </div>
                                    <div className="icon">
                                        <a href="https://t.me/Vardjaklap"><i className="fab fa-telegram-plane"></i></a>
                                    </div>
                                    <div className="icon">
                                        <a href="mailto:alexperesta@gmail.com"><i className="far fa-address-card"></i></a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


export default Card;