import React, { Component } from 'react';

const projects = [
    {
        name: "Fitonia",
        type: "Web application on AngularJS",
        description: "A small fitness application I made as my graduation project.",
        tech: "React",
        link: "https://vardjaklap.github.io/FitoniaFinal/"
    },
    {
        name: "Modest",
        type: "Landing page",
        description: "A landing, made from scratch from a template.",
        tech: "ddd",
        link: "https://vardjaklap.github.io/Modest/"
    }
    // {
    //     name: "dd2d",
    //     type: "ddd",
    //     description: "ddd",
    //     tech: "ddd",
    //     link: "sas"
    // },
];

function NumberList(props) {
    const projects = props.projects;
    const listItems = projects.map((project) =>
        <div className="workCard col-10 col-md-3" key={project.name} data-toggle="tooltip" data-placement="bottom" title={project.description}>
            <div className="mainInfoWork">
                <h2>{project.name}</h2>
                <p><span>{project.type}</span></p>
                <a href={project.link}><button className="findMoreWork">Find out more</button></a>
            </div>
        </div>
    );
    return (
        <div className="col-12 worksCards">
            {listItems}
        </div>
    );
}


export default class Works extends Component {


    componentDidMount() {

    }

    componentDidUpdate() {

    }
    render() {
        return (
            <section id="works">
                <div className="container">
                    <div className="row justify-content-center">
                        <h1 className="sectionHeader">My works</h1>
                    </div>
                    <div className="row justify-content-center">

                                <NumberList projects={projects} />

                    </div>
                </div>
            </section>
        );
    }
}