import React, { Component } from 'react';


class Contact extends Component {
    constructor(props) {
        super(props);
        this.state = {
            didMount: true,
            scrolledHead: false,
            isValidated: false,
            valueName: '',
            valueEmail: '',
            valueSubj: '',
            valueMess: '',
            errors: {},
            valueNameFilled: false,
            valueNameClass: 'inputEmpty',
            valueNameValid: false,
            valueEmailClass: 'inputEmpty',
            valueEmailValid: false,
            valuePhoneClass: 'inputEmpty',
            valuePhoneValid: false,
            valueSubjClass: 'inputEmpty',
            valueSubjValid: false,
            valueMessClass: 'inputEmpty',
            valueMessValid: false,
            replyValid: false,
            buttonShake: false,
            letterShown: false
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleClick(event){
        const target = event.target;
        const name = target.name;
        const validClass = name + 'Class';
        const validState = name + 'Valid';

        this.setState({
            [validClass]: 'green labelUp',
            [validState]: true,
            buttonShake: false

        });
    }
    handleChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;
        const full = name + 'Filled';
        const validClass = name + 'Class';
        const validState = name + 'Valid';

        if(target.id === 'name' || target.id === 'message'|| target.id === 'subject' ){
            if(value.length === 0){
                this.setState({
                    [validClass]: 'red inputEmpty',
                    [validState]: false
                });
            }else{
                if(value.length <= 1){
                    this.setState({
                        [validClass]: 'red inputNotEmpty',
                        [validState]: false
                    });
                }else{
                    this.setState({
                        [validClass]: 'green inputNotEmpty',
                        [validState]: true
                    });
                }
            }
        }
        if(target.id === 'email'){
            const emailRegex = /^\w+([-]?\w+)*@\w+([-]?\w+)*(\.\w{2,3})+$/;
            if(value.length === 0){
                this.setState({
                    [validClass]: 'red inputEmpty',
                    [validState]: false
                });
            }else{
                if(!emailRegex.test(value)){
                    this.setState({
                        [validClass]: 'red inputNotEmpty',
                        [validState]: false
                    });
                }else{
                    this.setState({
                        [validClass]: 'green inputNotEmpty',
                        [validState]: true
                    });
                }
            }
        }
        if(target.id === 'phone'){
            if(value.length === 0){
                this.setState({
                    [validClass]: 'red inputEmpty',
                    [validState]: false
                });
            }else{
                if(value.length >= 13){
                    this.setState({
                        [validClass]: 'red inputNotEmpty',
                        [validState]: false
                    });
                }else{
                    this.setState({
                        [validClass]: 'green inputNotEmpty',
                        [validState]: true
                    });
                }
            }

        }
        if(target.id === 'reply'){
            this.setState({
                replyValid: target.checked
            });
        }
        if(value !==''){
            this.setState({
                [full]: false
            });
        }
        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {

        if(this.state.valueNameValid && this.state.valueEmailValid && this.state.valueSubjValid && this.state.valueMessValid && this.state.replyValid){
            this.setState({
                letterShown: true
            });
            setTimeout(
                function() {
                    this.setState({letterShown: false});
                }
                    .bind(this),
                3000
            );
        }else{
            this.setState({
                buttonShake: true
            });
        }
        event.preventDefault();

    }
    componentDidMount() {
        window.addEventListener('scroll', () =>{
            let scrollPosHeader = window.pageYOffset | document.body.scrollTop;
            if(scrollPosHeader > 50) {
                this.setState({
                    scrolled: true
                })
            } else if(scrollPosHeader <= 50) {
                this.setState({
                    scrolled: false
                })
            }
        });
    }
    render() {
        return (
            <section id="contactMe">
                <div className={"letter " + (this.state.letterShown ? "letterShown" : "letterHidden")}>
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-md-6 cardLeaf">
                                <h1>Your letter</h1>
                                <div className="moreInfo">
                                    <div className="key">
                                        <p>From:</p>
                                        <p>Email:</p>
                                        <p>Phone:</p>
                                        <p>Subject:</p>
                                        <p>Message:</p>
                                    </div>
                                    <div className="value">
                                        <p>{this.state.radioValue} {this.state.valueName}</p>
                                        <p>{this.state.valueEmail}</p>
                                        <p>{this.state.valuePhone}</p>
                                        <p>{this.state.valueSubj}</p>
                                        <p>{this.state.valueMess}</p>
                                    </div>
                                </div>
                                <h3>You agreed to get my reply.
                                </h3>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="container">
                    <div className="row justify-content-center">
                        <h1 className="sectionHeader">Contact me</h1>
                    </div>
                    <div className="row justify-content-center">
                        <div className="col-10 col-md-5 cardLeaf">
                            <h3>I can even do the validation!</h3>
                            <form onSubmit={this.handleSubmit}>
                                <fieldset>
                                    <div>
                                        <input name="gender" type="radio" id="mr" value="mr" onClick={this.handleChange}/>
                                            <label htmlFor="mr">Mr.</label>
                                    </div>
                                    <div>
                                        <input name="gender" type="radio" id="ms" value="ms" required  onChange={this.handleChange}/>
                                        <label htmlFor="ms">Ms.</label>
                                    </div>
                                    <div>
                                        <input name="gender" type="radio" id="other" value="other" onChange={this.handleChange} />
                                        <label htmlFor="other">Other</label>
                                    </div>
                                </fieldset>
                                <div className="fName">
                                    <input type="text" id="name" name="valueName" required onChange={this.handleChange} onClick={this.handleClick}/>
                                    <span className={"bar " + (this.state.valueNameValid ? "yellowBar" : "redBar")}></span>
                                    <label htmlFor="name" className={this.state.valueNameClass.toString()} >Full name</label>
                                </div>
                                <div className="email">
                                    <input type="text" id="email" name="valueEmail" required onChange={this.handleChange} onClick={this.handleClick}/>
                                    <span className={"bar " + (this.state.valueEmailValid ? "yellowBar" : "redBar")}></span>
                                    <label htmlFor="email" className={this.state.valueEmailClass.toString()}>Email</label>
                                </div>
                                <div className="phone">
                                    <input type="number" id="phone" name="valuePhone" required onChange={this.handleChange} onClick={this.handleClick}/>
                                    <span className={"bar " + (this.state.valuePhoneValid ? "yellowBar" : "redBar")}></span>
                                    <label htmlFor="phone" className={this.state.valuePhoneClass.toString()}>Phone number</label>
                                </div>
                                <div className="subj">
                                    <input type="text" id="subject" name="valueSubj" required onChange={this.handleChange} onClick={this.handleClick}/>
                                    <span className={"bar " + (this.state.valueSubjValid ? "yellowBar" : "redBar")}></span>
                                    <label htmlFor="subject" className={this.state.valueSubjClass.toString()}>Subject</label>
                                </div>
                                <div className="mess">
                                    <textarea id="message" cols="1" rows="1" required name="valueMess" onChange={this.handleChange} onClick={this.handleClick}></textarea>
                                    <span className={"bar " + (this.state.valueMessValid ? "yellowBar" : "redBar")}></span>
                                    <label htmlFor="" className={this.state.valueMessClass.toString()}>Message</label>
                                </div>
                                <div className="replyMe">
                                    <input name="reply" type="checkbox" id="reply" value="reply" required onChange={this.handleChange}/>
                                        <label htmlFor="reply">I want to get to know you!</label>
                                </div>
                                <button className={this.state.buttonShake ? "buttonShake" : " "}>Send</button>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}





export default Contact;