import React, { Component } from 'react';


export default class Interests extends Component {
    componentDidMount() {
        window.$('[data-toggle="tooltip"]').tooltip();
    }

    componentDidUpdate() {
        window.$('[data-toggle="tooltip"]').tooltip();
    }
    render() {
        return (
            <section id="interests">
                <div className="container">
                    <div className="row justify-content-center">
                        <h1 className="sectionHeader">My interests</h1>
                    </div>
                    <div className="row justify-content-center">
                        <div className="col-10 cardLeaf">
                            <p>Life is an experience. That is why I want to experience it, to feel it. We are given a wide range of emitions, so why not to feel them all? Thrill before the adventure, inspiration from creativity, hope in face of danger. Even though human life is short, we can get memories worth of lifetimes.</p>
                            <div className="row justify-content-center">
                                <div className="boxInt" data-toggle="tooltip" data-placement="bottom" title="Fitness"><i className="fas fa-dumbbell"></i></div>
                                <div className="boxInt" data-toggle="tooltip" data-placement="bottom" title="Photography"><i className="fas fa-camera-retro"></i></div>
                                <div className="boxInt" data-toggle="tooltip" data-placement="bottom" title="Traveling"><i className="fas fa-suitcase"></i></div>
                                <div className="boxInt" data-toggle="tooltip" data-placement="bottom" title="Cooking"><i className="fas fa-utensils"></i></div>
                                <div className="boxInt" data-toggle="tooltip" data-placement="bottom" title="Reading books"><i className="fas fa-book"></i></div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}