import React, { Component } from 'react';


class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            didMount: true,
            scrolledHead: false
        };
    }
    scrollContact(){
        let test = document.getElementById("contactMe");
        window.scroll({
            top: test.offsetTop,
            left: 0,
            behavior: 'smooth'
        });
    }
    scrollAbout(){
        let test = document.getElementById("skills");
        window.scroll({
            top: test.offsetTop,
            left: 0,
            behavior: 'smooth'
        });
    }
    scrollWorks(){
        let test = document.getElementById("works");
        window.scroll({
            top: test.offsetTop - 20,
            left: 0,
            behavior: 'smooth'
        });
    }
    scrollInterests(){
        let test = document.getElementById("interests");
        window.scroll({
            top: test.offsetTop,
            left: 0,
            behavior: 'smooth'
        });
    }
    scrollTop(){
        window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
    }
    componentDidMount() {
        window.addEventListener('scroll', () =>{
            let scrollPosHeader = window.pageYOffset | document.body.scrollTop;
                if(scrollPosHeader > 50) {
                    this.setState({
                        scrolled: true
                    })
                } else if(scrollPosHeader <= 50) {
                    this.setState({
                        scrolled: false
                    })
                }
        });
    }
    render() {
        return (
            <header>
                <div className="pictureHeader">
                    <div className="overlayHeader"></div>
                </div>
                <div className={this.state.scrolled ? "nav scrolled" : "nav unscrolled"}>
                    {/*<div className="return" data-toggle="tooltip" data-placement="bottom" title="Return to the main page">*/}
                        {/*<i className="fas fa-arrow-left"></i>*/}
                    {/*</div>*/}
                    <div className="container">
                        <div className="row">
                            <div className="col-12">
                                <ul className={this.state.scrolled ? "scrolledUl" : "unScrolledUl"}>
                                    <li><div className="navLink"><a href="#1" className={this.state.scrolled ? "scrolledA" : "unScrolledA"} onClick={this.scrollAbout}>About</a></div> <div className="lineNav"></div></li>
                                    <li><div className="navLink"><a href="#2" className={this.state.scrolled ? "scrolledA" : "unScrolledA"} onClick={this.scrollWorks}>Works</a></div><div className="lineNav"></div></li>
                                    <li><div className="navLink"><a href="#3" className={this.state.scrolled ? "scrolledA" : "unScrolledA"} onClick={this.scrollInterests}>Interests</a></div><div className="lineNav"></div></li>
                                    <li><div className="navLink"><a href="#4" className={this.state.scrolled ? "scrolledA" : "unScrolledA"} onClick={this.scrollContact}>Contact</a></div><div className="lineNav"></div></li>
                                    <li><div className="navLink"><a href="#5" className={this.state.scrolled ? "scrolledA" : "unScrolledA"} onClick={this.scrollAbout}>More</a></div><div className="lineNav"></div></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={this.state.scrolled ? "upButton" : "noDisplay"}>
                    <a href="#0" className="scrollUp" onClick={this.scrollTop}><i className="fas fa-arrow-up"></i></a>
                </div>
            </header>
        );
    }
}





export default Header;