import React, { Component } from 'react';
import photoOne from '../img/photo1.png'
import photoTwo from '../img/photo2.png'
import photoThree from '../img/photo3.png'
import photoFour from '../img/photo4.png'
import photoFive from '../img/photo5.png'
import photoSix from '../img/photo6.png'
import photoSeven from '../img/photo7.png'
import photoEight from '../img/photo8.png'
import photoNine from '../img/photo9.png'


export default class Photos extends Component {
    componentDidMount() {
        window.$('.carousel').carousel({
            // interval: 2000
        })
    }

    componentDidUpdate() {
        window.$('.carousel').carousel({
            // interval: 2000
        })
    }
    render() {
        return (
            <section id="photos">
                <div className="container">
                    <div className="row justify-content-center">
                        <h1 className="sectionHeader">My photos</h1>
                    </div>
                    <div className="row justify-content-center">
                        <div className="col-9 carousels">
                            <div className="mainCar">
                                <div id="carouselMain" className="carousel slide" data-ride="carousel">
                                    <div className="carousel-inner">
                                        <div className="carousel-item active">
                                            <img className="d-block w-100" src={photoEight} alt="First slide"/>
                                        </div>
                                        <div className="carousel-item">
                                            <img className="d-block w-100" src={photoNine} alt="First2 slide"/>
                                        </div>
                                        <div className="carousel-item">
                                            <img className="d-block w-100" src={photoFive} alt="First3 slide"/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="otherCar">
                                <div id="carouselMainTwo" className="carousel slide" data-ride="carousel">
                                    <div className="carousel-inner">
                                        <div className="carousel-item active">
                                            <img className="d-block w-100" src={photoOne} alt="First slide"/>
                                        </div>
                                        <div className="carousel-item">
                                            <img className="d-block w-100" src={photoFour} alt="First2 slide"/>
                                        </div>
                                        <div className="carousel-item">
                                            <img className="d-block w-100" src={photoSeven} alt="First3 slide"/>
                                        </div>
                                    </div>
                                </div>
                                <div id="carouselMainThree" className="carousel slide" data-ride="carousel">
                                    <div className="carousel-inner">
                                        <div className="carousel-item active">
                                            <img className="d-block w-100" src={photoSix} alt="First slide"/>
                                        </div>
                                        <div className="carousel-item">
                                            <img className="d-block w-100" src={photoThree} alt="First2 slide"/>
                                        </div>
                                        <div className="carousel-item">
                                            <img className="d-block w-100" src={photoTwo} alt="First3 slide"/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </section>
        );
    }
}