import React, { Component } from 'react';


export default class Skills extends Component {
    constructor(props) {
        super(props);
        this.state = {
            scrolled: false,
            scrolledHead: false
        };
        this.handleClick = this.handleClick.bind(this);
    }
    handleClick(){
        window.open('https://drive.google.com/open?id=1TXHl6Tzpd0g1ngH7YF3A-P_ntATYMEXy')
    }
    componentDidMount() {
        window.addEventListener('scroll', () =>{
            let scrollPosY = window.pageYOffset | document.body.scrollTop;
                if(scrollPosY > 500) {
                    this.setState({
                        scrolled: true,
                        scrolledHead: true
                    })
                } else if(scrollPosY <= 300) {
                    this.setState({
                        scrolled: false
                    })
                }
        });
    }
    render() {
        return (
            <section id="skills">
                <div className="container">
                    <div className="row justify-content-center">
                                <button className="downloadResume" onClick={this.handleClick}> Download resume</button>
                    </div>
                    <div className="row justify-content-center">
                        <h1 className="sectionHeader">Professional skills</h1>
                    </div>
                    <div className="row justify-content-center">
                        <div className="col-10 cardLeaf skillsCard">
                            <div className="row">
                                <div className="col-6">
                                    <h5>HTML</h5>
                                    <div className="progressLine">
                                        <div className="progress">
                                            <div className={this.state.scrolled ? "progress-bar progressHtml" : "progress-bar"} role="progressbar" aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-6">
                                    <h5>CSS3/SCSS</h5>
                                    <div className="progressLine">
                                        <div className="progress">
                                            <div className={this.state.scrolled ? "progress-bar progressCss" : "progress-bar"} role="progressbar" aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-6">
                                    <h5>JavaScript</h5>
                                    <div className="progressLine">
                                        <div className="progress">
                                            <div className={this.state.scrolled ? "progress-bar progressJs" : "progress-bar"} role="progressbar" aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-6">
                                    <h5>JQuery</h5>
                                    <div className="progressLine">
                                        <div className="progress">
                                            <div className={this.state.scrolled ? "progress-bar progressJq" : "progress-bar"} role="progressbar" aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-6">
                                    <h5>AngularJS</h5>
                                    <div className="progressLine">
                                        <div className="progress">
                                            <div className={this.state.scrolled ? "progress-bar progressAng" : "progress-bar"} role="progressbar" aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-6">
                                    <h5>React</h5>
                                    <div className="progressLine">
                                        <div className="progress">
                                            <div className={this.state.scrolled ? "progress-bar progressReact" : "progress-bar"} role="progressbar" aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}





// export default Skills;